import React from 'react';
import { CardRent } from 'components';
import { RentList, Rent } from './styled'
const Component = ({ dataRents }) => {
    const dataVerify = dataRents.length > 0;

    return (
        <RentList>
            {dataVerify ? null : <div><h1>No has rentado ningun producto</h1> <a href="/">hazlo ahora</a></div>}

            {dataRents.map(producto =>
                <Rent key={`product-${producto.idProduct}`}>
                    <CardRent
                        dataProduct={dataRents.dataProduct}
                        rentDetails={dataRents.rentDetails}
                        dataLessor={dataRents.dataLessor}
                    />
                </Rent>
            )}
        </RentList>
    );
}

export default Component;
export { Component as RentList }